import React, { useState } from "react";
import PropTypes from "prop-types";
function AddTodo(props) {
  const [title, setTitle] = useState("");
  const chaneInput = (e) => {
    setTitle(e.target.value);
  };
  const addTodolist = props.addTodoFunc;
  const addSingleTodo = (e) => {
    e.preventDefault();
    if (title !== "") {
      addTodolist(title);
      setTitle("");
    }
  };
  return (
    <div>
      <form action='' onSubmit={addSingleTodo} className='add'>
        <input
          type='text'
          name='title'
          placeholder='add todo'
          className='textAdd'
          onChange={chaneInput}
          value={title}
        />
        <input type='submit' value='add' className='submitAdd' />
      </form>
    </div>
  );
}
AddTodo.propTypes = {
  addTodoFunc: PropTypes.func.isRequired,
};
export default AddTodo;
